import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import conf from "../../config.json";
import Modal from "../../components/Modal"
import $ from "jquery"


export default function ViewJobApplication() {
    const data = useLocation().state.data;
    const navigate = useNavigate();

    function base64ToArrayBuffer(base64) {
        var binaryString = window.atob(base64);
        var binaryLen = binaryString.length;
        var bytes = new Uint8Array(binaryLen);
        for (var i = 0; i < binaryLen; i++) {
            var ascii = binaryString.charCodeAt(i);
            bytes[i] = ascii;
        }
        return bytes;
    }

    function saveByteArray(reportName, byte) {
        let blob = new Blob([byte], { type: "application/pdf" });
        let link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        let fileName = reportName;
        link.download = fileName;
        link.click();
    };

    const downloadFile = (resume, fileName) => {
        const dataArr = base64ToArrayBuffer(resume);
        saveByteArray(fileName, dataArr);
    }

    const handleDelete = () => {
        const formData = new FormData();
        formData.append("applicant_id", data.id);

        const header = {
            "Authorization": "Bearer " + localStorage.getItem("accessToken")
        }

        $.ajax({
            url: conf.api_endpoint.apply,
            type: 'DELETE',
            headers: header,
            data: formData,
            mimeType: "multipart/form-data",
            processData: false,
            contentType: false,
            success: function (response) {
                if (JSON.parse(response).success) {
                    navigate('/a/job-applications',
                        {
                            state: data.first_name + " " + data.last_name + "'s Application Deleted Succesfully"
                        })
                }
            },
            error: function (errors) {
            }
        });
    }

    return (
        <>
            <div className="pb-3 border-bottom d-flex align-items-center">
                <p className="h4 mb-0 me-3">Actions : </p>
                <button
                    type="button"
                    className="btn btn-sm btn-danger"
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal">
                    <i className="bi bi-x-mark"></i>
                    Delete
                </button>
            </div>

            <Modal data={{
                head_title: "Confirm Delete",
                body_message: "Are you sure to delete : " + data.first_name + " " + data.last_name + "'s Application",
                click_handle: handleDelete,
                click_message: "Delete"
            }} />

            <div className="mb-4 border-bottom py-3">
                <h4 className="c-primary">{data.job_id}</h4>
                <p className="c-gray h5">{data.first_name} {data.last_name}</p>
                <p className="mb-0">{data.phone}</p>
                <p className="mb-0">{data.email}</p>
            </div>

            <div className="py-2">
                <h5>Resume</h5>
                <button
                    className="d-flex justify-content-center align-items-center p-4 b-light w-25 rounded-4 border-0 my-3"
                    onClick={() => downloadFile(data.resume, data.resume_filename)}
                >
                    <div>
                        <i className="bi bi-filetype-pdf fs-3 mb-2"></i>
                        <div>{data.resume_filename}</div>

                    </div>
                </button>
            </div>
        </>
    )
}