import React, { useState, useEffect } from "react";
import Header from "../assist/Header"
import Footer from "../assist/Footer"
import Banner from "../assist/Banner"
import banner_image from "../assets/images/career_banner.webp"
import { useParams, Link } from "react-router-dom";
import content from "../content.json"
import ServicesCard from "../components/ServicesCard"


export default function Services() {
    const prime_services = content.primeServices;
    const other_services = content.otherServices;
    const [isHome, setIsHome] = useState(true);
    let { id, type } = useParams();
    const [data, setData] = useState({});
    
    
    useEffect(() => {
        setIsHome(true);

        if (id && type) {
            setIsHome(false);
            content[type].forEach(element => {
                if (element.id === id)
                    setData(element)
            });

        }
    }, [id, type])

    return (
        <>
            <Header />
            <div className="b-light pb-5" style={{ 'minHeight': 'calc(80vh - 70px)' }}>
                <Banner banner_source={banner_image} banner_alt={'Careers Banner'} data={"Our Services"} />
                {
                    isHome &&
                    <>
                        <section>
                            <div className="container">
                                <div className="text-center my-5">
                                    <span className="h3 pb-2 border-bottom border-2 c-secondary">Prime Services</span>
                                </div>
                                <div className="row">
                                    {
                                        prime_services.map((data, i) => {
                                            return (
                                                <ServicesCard data={data} key={i} type={ "primeServices" } />
                                            )
                                        })
                                    }
                                </div>
                            </div>
                        </section>

                        <section className="b-light py-5 mt-5" id="services">
                            <div className="container">
                                <div className="text-center mb-5">
                                    <span className="h3 pb-2 border-bottom border-2 c-secondary">Other Services</span>
                                </div>
                                <div className="container-fluid">
                                    <div className="row">
                                        {
                                            other_services.map((data, i) => {
                                                return (
                                                    <ServicesCard data={data} key={i} type={"otherServices"} />
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            </div>
                        </section>
                    </>
                }

                {
                    type === "primeServices" &&
                    <section>
                        <div className="container">
                            <div className="text-center mb-5">
                                <span className="h3 pb-2 border-bottom border-2 c-secondary"></span>
                            </div>
                            <h4 className="fw-normal c-primary">{data.heading}</h4>
                            <p className="fst-italic">{data.description}</p>
                            <p>{data.detail}</p>
                        </div>
                    </section>
                }

                {
                    type === "otherServices" &&
                    <section>
                        <div className="container">
                            <div className="text-center mb-5">
                                <span className="h3 pb-2 border-bottom border-2 c-secondary"></span>
                            </div>
                            <h4 className="fw-normal c-primary">{data.heading}</h4>
                            <p className="fst-italic">{data.description}</p>
                            <p>{data.detail}</p>
                        </div>
                    </section>
                }

                <div className="container text-center">
                    <Link className="btn btn-primary rounded-0 border-0 b-primary" to={"/contact"}>Contact us</Link>
                </div>
            </div>
            <Footer />
        </>
    );
}