import React, { useState, useEffect } from "react";
import Header from "../assist/Header";
import Banner from "../assist/Banner";
import Footer from "../assist/Footer";
import JobAccordion from "../components/JobAccordion";
import banner_image from "../assets/images/career_banner.webp";
import conf from "../config.json"
import $ from "jquery";


export default function Careers() {
    const baseUrl = conf.api_endpoint.careers;
    const [joblist, setJobList] = useState([]);
    const [no_data, setNodata] = useState(false);

    useEffect(() => {
        $.ajax({
            url: baseUrl,
            method: 'GET',
            success: function (response) {
                if (response.success) {
                    if (response.response) {
                        response.response.forEach(element => {
                            setJobList(joblist => [
                                ...joblist,
                                element
                            ]);
                        });
                    }
                    else {
                        setNodata(true);
                    }
                }
            }
        });
    }, [baseUrl]);

    return (
        <>
            <Header />
            <Banner banner_source={banner_image} banner_alt={'Careers Banner'} data={"Careers"} />
            <div className="mt-5 text-center">
                <h1 className="c-secondary fw-normal">Work with the most amazing "team"</h1>
                <h4 className="fw-normal my-3 c-gray">Apply for what keeps you going</h4>
            </div>
            <div className="container mt-5 footer-space">
                <div>
                    <JobAccordion joblist={joblist} />
                    {no_data &&
                        <div className="text-center">
                            <h4 className="c-secondary fw-normal">No Data</h4>
                        </div>
                    }
                </div>
            </div>
            <Footer />
        </>
    )

}