import { useState, useRef } from "react";
import { useNavigate } from 'react-router-dom';
import { Editor } from '@tinymce/tinymce-react';
import $ from "jquery"
import conf from "../config.json"


export default function JobCreateForm() {
    const today = new Date();
    const posted_on = today.toLocaleDateString("en-CA");
    const navigate = useNavigate();
    const [inputs, setInputs] = useState({ posted_on: posted_on });
    const [errors, setErrors] = useState({});
    const responsibilitiesRef = useRef(null);
    const descriptionRef = useRef(null);
    const qualificationRef = useRef(null);

    const handleChange = (e) => {
        setInputs(inputs => ({ ...inputs, [e.target.name]: e.target.value }));
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setErrors({})

        const header = {
            "Authorization": "Bearer " + localStorage.getItem("accessToken")
        }

        console.log(inputs);

        $.ajax({
            url: conf.api_endpoint.careers,
            method: "POST",
            headers: header,
            data: JSON.stringify(inputs),
            success: (response) => {
                if (response.success) {
                    navigate('/a/jobs', { state: inputs.title + " - Created Successfully" });
                }
            },
            error: (errors) => {
                if (errors.status === 401) {
                    navigate("/login")
                }
                else {
                    const err = errors.responseJSON.errors;
                    console.log(err)
                    err.forEach(element => {
                        setErrors(er => ({ ...er, [element.field]: element.message }))
                    });
                }
            }
        });
    }

    const handleEditorChange = (name, editorRef) => {
        setInputs(inputs => ({ ...inputs, [name]: editorRef.current.getContent() }));
    }


    return (
        <>
            <div className="container-fluid ">
                <div className="col-12 col-md-8">
                    <div className="mb-5">
                        <span className="h3 pb-2 border-bottom">Create Job</span>
                    </div>
                    
                    <form onSubmit={handleSubmit}>
                        <div className="mb-4">
                            <label htmlFor="title" className="form-label">Job Title <span className="text-danger">*</span> </label>
                            <input
                                type="text"
                                name="title"
                                id="title"
                                className="form-control"
                                placeholder="Backend Developer"
                                value={inputs.title || ""}
                                onChange={handleChange}
                            />
                            {
                                errors.Title &&
                                <small className="text-danger">{errors.Title}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="job_type" className="form-label">Job Type <span className="text-danger">*</span> </label>
                            <select className="form-select" onChange={handleChange} name="job_type">
                                <option value="">Select Job Type</option>
                                <option value="FullTime">Full Time</option>
                                <option value="PartTime">Part Time</option>
                                <option value="Remote">Remote</option>
                                <option value="Onsite">Onsite</option>
                            </select>
                            {
                                errors.JobType &&
                                <small className="text-danger">{errors.JobType}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="location" className="form-label">Location <span className="text-danger">*</span> </label>
                            <input
                                type="text"
                                name="location"
                                id="location"
                                className="form-control"
                                placeholder="Delhi"
                                value={inputs.location || ""}
                                onChange={handleChange}
                            />
                            {
                                errors.Location &&
                                <small className="text-danger">{errors.Location}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="valid_upto" className="form-label">Valid Upto <span className="text-danger">*</span> </label>
                            <input
                                type="date"
                                name="valid_upto"
                                id="valid_upto"
                                className="form-control"
                                placeholder=""
                                value={inputs.valid_upto || ""}
                                onChange={handleChange}
                            />
                            {
                                errors.ValideUpto &&
                                <small className="text-danger">{errors.ValideUpto}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="experience" className="form-label">Experience <span className="text-danger">*</span> </label>
                            <input
                                type="text"
                                name="experience"
                                id="experience"
                                className="form-control"
                                placeholder="1-2 years"
                                value={inputs.experience || ""}
                                onChange={handleChange}
                            />
                            {
                                errors.Experience &&
                                <small className="text-danger">{errors.Experience}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="job_description" className="form-label">Description <span className="text-danger">*</span> </label>
                            <Editor
                                onInit={(evt, editor) => descriptionRef.current = editor}
                                init={{
                                    height: 300,
                                    menubar: true,
                                    plugins: 'preview importcss searchreplace autolink autosave save directionality code visualblocks visualchars fullscreen image link media codesample table charmap pagebreak nonbreaking anchor insertdatetime advlist lists wordcount help charmap quickbars emoticons',
                                    toolbar: 'undo redo | bold removeformat image | numlist bullist | fullscreen  preview | fontselect fontsizeselect styleselect | italic forecolor backcolor outdent indent formatselect codesample | hr alignleft aligncenter alignright alignjustify charmap emoticons | image media link anchor underline strikethrough',
                                    content_style: 'body { font-family: Helvetica, Arial, sans-serif; font-size:14px }'
                                }}
                                onEditorChange={() => handleEditorChange("job_description", descriptionRef)}
                            />
                            {
                                errors.JobDescription &&
                                <small className="text-danger">{errors.JobDescription}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="responsibilities" className="form-label">Responsibilities <span className="text-danger">*</span> </label>
                            <Editor
                                onInit={(evt, editor) => responsibilitiesRef.current = editor}
                                init={{
                                    height: 300,
                                    menubar: true,
                                    plugins: 'preview importcss searchreplace autolink autosave save directionality code visualblocks visualchars fullscreen image link media codesample table charmap pagebreak nonbreaking anchor insertdatetime advlist lists wordcount help charmap quickbars emoticons',
                                    toolbar: 'undo redo | bold removeformat image | numlist bullist | fullscreen  preview | fontselect fontsizeselect styleselect | italic forecolor backcolor outdent indent formatselect codesample | hr alignleft aligncenter alignright alignjustify charmap emoticons | image media link anchor underline strikethrough',
                                    content_style: 'body { font-family: Helvetica, Arial, sans-serif; font-size:14px }'
                                }}
                                onEditorChange={() => handleEditorChange("responsibilities", responsibilitiesRef)}
                            />
                            {
                                errors.Responsibility &&
                                <small className="text-danger">{errors.Responsibility}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <label htmlFor="qualifications" className="form-label">Qualifications <span className="text-danger">*</span> </label>
                            <Editor
                                onInit={(evt, editor) => qualificationRef.current = editor}
                                init={{
                                    height: 300,
                                    menubar: true,
                                    plugins: 'preview importcss searchreplace autolink autosave save directionality code visualblocks visualchars fullscreen image link media codesample table charmap pagebreak nonbreaking anchor insertdatetime advlist lists wordcount help charmap quickbars emoticons',
                                    toolbar: 'undo redo | bold removeformat image | numlist bullist | fullscreen  preview | fontselect fontsizeselect styleselect | italic forecolor backcolor outdent indent formatselect codesample | hr alignleft aligncenter alignright alignjustify charmap emoticons | image media link anchor underline strikethrough',
                                    content_style: 'body { font-family: Helvetica, Arial, sans-serif; font-size:14px }'
                                }}
                                onEditorChange={() => handleEditorChange("qualifications", qualificationRef)}
                            />
                            {
                                errors.Qualifications &&
                                <small className="text-danger">{errors.Qualifications}</small>
                            }
                        </div>

                        <div className="mb-4">
                            <button type="submit" className="btn b-primary c-light">Create Job</button>
                        </div>
                    </form>
                </div>
            </div>
        </>
    )
}