import React, { useState } from "react";
import Header from "../assist/Header";
import Footer from "../assist/Footer";
import logo from "../assets/images/logo.png"
import $ from "jquery"
import conf from "../config.json"
import { useNavigate } from 'react-router-dom';


export default function LoginForm() {
    const [submitting, setSubmitting] = useState(false);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [formStatus, setFormStatus] = useState(false);
    const navigate = useNavigate();

    function validateForm() {
        return email.length > 0 && password.length > 0;
    }

    function create_session(token, email) {
        localStorage.setItem("is_authenticated", true);
        localStorage.setItem("accessToken", token);
        localStorage.setItem("user", email);
    }

    function handleSubmit(event) {
        setSubmitting(true);
        event.preventDefault();
        const cred = {
            email: email,
            password: password
        }

        $.ajax({
            url: conf.api_endpoint.auth,
            method: 'POST',
            data: cred,
            success: async (response) => {
                if (response.success) {
                    setFormStatus(true);
                    create_session(response.response, email);
                    navigate("/admin");
                }
            },
            error: (errors) => {
                console.log(errors);
            }
        })
    }

    return (
        <>
            <Header />
            <div className="text-center footer-space pt-5" style={{ background: '#f5f5f5' }}>
                <main className="form-login">
                    <form onSubmit={handleSubmit}>
                        <img src={logo} alt="" width={80} className="form-fluid my-4" />
                        {
                            formStatus &&
                            <div className="alert alert-success">
                                <p className="mb-0">
                                    <i className="bi bi-check-circle me-2"></i>
                                    Login successful! You will be redirected.
                                </p>
                            </div>
                        }

                        <div className="form-floating mb-2">
                            <input
                                type="email"
                                className="form-control"
                                id="floatingInput"
                                placeholder="name@example.com"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                            />
                            <label htmlFor="floatingInput">Email address</label>
                        </div>
                        <div className="form-floating">
                            <input
                                type="password"
                                className="form-control"
                                id="floatingPassword"
                                placeholder="Password"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                            />
                            <label htmlFor="floatingPassword">Password</label>
                        </div>
                        <br />
                        <button className="w-100 btn btn-lg btn-primary" type="submit" disabled={!validateForm()}>
                            {submitting &&
                                <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                            }
                            Login</button>
                    </form>
                </main>
            </div>
            <Footer />
        </>
    )
}