import { useLocation } from 'react-router-dom';
import React, { useState } from "react";
import Header from "../assist/Header";
import Footer from "../assist/Footer";
import logo from "../assets/images/logo.png"
import $ from "jquery";
import conf from "../config.json"
import { useNavigate } from 'react-router-dom';

const Submitted = () => {
    return (
        <>
            <div className="text-center py-5">
                <h3><i className='text-success bi bi-check-circle'></i></h3>
                <p className='text-success'>Submitted</p>
                <p className='text-secondary'>We will contact you Shortly.</p>
            </div>
        </>
    )
}

const Form = ({ inputs, errors, handleChange, handleFileChange, submitForm }) => {
    return (
        <>
            <form onSubmit={submitForm} encType='multipart/form-data'>
                <div className="mb-4">
                    <label htmlFor="first_name" className="mb-2">First Name <span className="text-danger">*</span></label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="First Name"
                        name="first_name"
                        id="first_name"
                        value={inputs.first_name || ""}
                        onChange={handleChange}
                    />
                    {
                        errors.FirstName &&
                        <small className="text-danger">{errors.FirstName}</small>
                    }
                </div>
                <div className="mb-4">
                    <label htmlFor="last_name" className="mb-2"> Last Name</label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Last Name"
                        name="last_name"
                        id="last_name"
                        value={inputs.last_name || ""}
                        onChange={handleChange}
                    />
                    {
                        errors.LastName &&
                        <small className="text-danger">{errors.LastName}</small>
                    }
                </div>
                <div className="mb-4">
                    <label htmlFor="email" className="mb-2">Email <span className="text-danger">*</span></label>
                    <input
                        type="email"
                        className="form-control"
                        placeholder="email@example.com"
                        name="email"
                        id="email"
                        value={inputs.email || ""}
                        onChange={handleChange}

                    />
                    {
                        errors.Email &&
                        <small className="text-danger">{errors.Email}</small>
                    }
                </div>
                <div className="mb-4">
                    <label htmlFor="phone" className="mb-2">Phone <span className="text-danger">*</span></label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Phone"
                        name="phone"
                        id="phone"
                        value={inputs.phone || ""}
                        onChange={handleChange}
                        pattern="^[6-9]{1}[0-9]{9}$"

                    />
                    {
                        errors.Phone &&
                        <small className="text-danger">{errors.Phone}</small>
                    }
                </div>
                <div className="mb-4">
                    <label htmlFor="resume" className="mb-2">Latest Resume <span className="text-danger">*</span></label>
                    <input
                        type="file"
                        className="form-control"
                        accept=".pdf"
                        name="resume"
                        id="resume"
                        onChange={handleFileChange}
                    />
                    {
                        errors.ResumeFile &&
                        <small className="text-danger">{errors.ResumeFile}</small>
                    }
                </div>
                <div className="d-flex mt-5">
                    <button type="reset" className="btn btn-secondary w-100 me-3">Reset</button>
                    <input type="submit" className="btn btn-primary w-100" value="Apply" />
                </div>
            </form>
        </>
    )
}


export default function JobForm() {
    const job_data = useLocation().state;
    const [inputs, setInputs] = useState({});
    const [file, setFile] = useState(null);
    const [errors, setErrors] = useState({});
    const [formStatus, setFormStatus] = useState(false);
    const navigate = useNavigate();

    const handleChange = (e) => {
        setInputs(inputs => ({ ...inputs, [e.target.name]: e.target.value }));
    }

    const handleFileChange = (event) => {
        setFile(event.target.files[0]);
    };

    const submitForm = (e) => {
        e.preventDefault();
        setErrors({});

        const formData = new FormData();
        if (file) {
            formData.append('resume', file);
        }
        formData.append('job_id', job_data.id);
        for (var key in inputs) {
            formData.append(key, inputs[key]);
        }

        $.ajax({
            url: conf.api_endpoint.apply,
            method: "POST",
            data: formData,
            success: (response) => {
                if (response.success) {
                    setFormStatus(true);
                    setTimeout(() => {
                        setFormStatus(false);
                        navigate('/careers');
                    }, 5000);
                }
            },
            error: (errors) => {
                const err = errors.responseJSON.errors;
                err.forEach(element => {
                    setErrors(er => ({ ...er, [element.field]: element.message }))
                });
            },
            processData: false,
            contentType: false
        });
    }

    return (
        <>
            <Header />
            <div className="container pt-4">
                <div className="col-md-8 col-lg-6 m-auto b-light rounded-3 p-4 my-4 border-top border-success border-4">
                    <div className="text-center mb-5">
                        <img src={logo} alt="Logo" width={80} className="img-fluid" />
                        <br /> <br />
                        <p className="h4 text-center">Job Application</p >
                        <p>{job_data.title}</p>
                    </div>

                    {
                        !formStatus ?
                            <Form
                                inputs={inputs}
                                errors={errors}
                                handleChange={handleChange}
                                handleFileChange={handleFileChange}
                                submitForm={submitForm}
                            /> :
                            <Submitted />
                    }

                </div>
            </div>
            <Footer />
        </>
    )
}