import React from "react"
import { useLocation, useNavigate } from "react-router-dom"
import $ from "jquery"
import conf from "../../config.json"
import Modal from "../../components/Modal"


export default function ViewJob() {
    const data = useLocation().state.data;
    const navigate = useNavigate();

    const handleDelete = () => {
        const formData = new FormData();
        formData.append("contact_id", data.id);

        const header = {
            "Authorization": "Bearer " + localStorage.getItem("accessToken")
        }

        $.ajax({
            url: conf.api_endpoint.contact,
            type: 'DELETE',
            headers: header,
            data: formData,
            mimeType: "multipart/form-data",
            processData: false,
            contentType: false,
            success: function (response) {
                if (JSON.parse(response).success) {
                    navigate('/a/contact-messages/', { state: data.subject + " - Deleted Succesfully" })
                }
            },
            error: function (errors) {
            }
        });
    }

    return (
        <>
            <Modal data={{
                head_title: "Confirm Delete",
                body_message: "Are you sure to delete : " + data.subject,
                click_handle: handleDelete,
                click_message: "Delete"
            }} />

            <div className="pb-3 border-bottom d-flex align-items-center">
                <p className="h4 mb-0 me-3">Actions : </p>
                <button
                    type="button"
                    className="btn btn-sm btn-danger"
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal">
                    <i className="bi bi-x-mark"></i>
                    Delete
                </button>
            </div>

            <div className="mb-4 border-bottom py-3">
                <h4 className="c-primary">{data.subject}</h4>
                <p className="c-gray mb-0">{data.full_name} &bull; {data.email}</p>
            </div>

            <div className="py-2">
                <h5>Message</h5>
                <div>{data.message}</div>
            </div>
        </>
    )
}