import React, { useState, useEffect } from "react";
import $ from "jquery"
import conf from "../config.json"
import { Link, useNavigate } from "react-router-dom";


export default function JobData() {
    const navigate = useNavigate();
    const [joblist, setJobList] = useState([]);
    const [message, setMessage] = useState('');

    function handleClick(data) {
        navigate('/a/jobs/view', { state: data });
    }

    useEffect(() => {
        $.ajax({
            url: conf.api_endpoint.careers,
            method: 'GET',
            success: function (response) {
                if (response.success) {
                    if (response.response) {
                        response.response.forEach(element => {
                            setJobList(joblist => [
                                ...joblist,
                                element
                            ]);
                        });
                    }
                    else {
                        setMessage("No Data to Show")
                    }
                }
            },
            error: function (errors) {
                setMessage("Something went wrong! Try Refresh or Contact adminstrator");
            }
        });
    }, []);

    setTimeout(() => {
        setMessage("");
    }, 3000)

    return (
        <>
            {
                message &&
                <div className="alert alert-warning py-2 px-3">
                    <p className="mb-0">{message}</p>
                </div>
            }
            <div className="mb-4 d-flex justify-content-between">
                <span className="pb-2 h4 border-bottom">Jobs</span>
                <div>
                    <Link to={"/a/jobs/create"} className="btn btn-success btn-sm"> <i className="bi bi-plus"></i> New Job</Link>
                </div>
            </div>
            <div>
                <table className="table border table-striped table-hover table-sm">
                    <thead className="table-dark">
                        <tr>
                            <th scope="col" className="col-1">S.no</th>
                            <th scope="col" >Title</th>
                            <th scope="col" className="col-3">Posted On</th>
                            <th scope="col" className="col-3">Last Date</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            joblist.map((data, i) => {
                                return (
                                    <tr key={i}>
                                        <th scope="row">{i + 1}</th>
                                        <td>
                                            <button
                                                className="btn btn-link py-0"
                                                onClick={() => handleClick({
                                                    data: data,
                                                })}
                                            >{data.title}</button>
                                        </td>
                                        <td>{data.posted_on}</td>
                                        <td>{data.valid_upto}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}