import React from "react";
import { useLocation, useNavigate } from "react-router-dom";
import parse from 'html-react-parser';
import $ from "jquery";
import conf from "../../config.json"
import Modal from "../../components/Modal";


export default function ViewJob() {
    const data = useLocation().state.data;
    const navigate = useNavigate();

    const handleDelete = () => {
        const formData = new FormData();
        formData.append("job_id", data.id);

        const header = {
            "Authorization": "Bearer " + localStorage.getItem("accessToken")
        }

        $.ajax({
            url: conf.api_endpoint.careers,
            type: 'DELETE',
            headers: header,
            data: formData,
            mimeType: "multipart/form-data",
            processData: false,
            contentType: false,
            success: function (response) {
                if (JSON.parse(response).success) {
                    navigate('/a/jobs', { state: data.title + " - Deleted Succesfully" })
                }
            },
            error: function (errors) {
            }
        });
    }

    return (
        <>
            <div className="pb-3 border-bottom d-flex align-items-center">
                <p className="h4 mb-0 me-3">Actions : </p>
                <button
                    className="btn btn-sm btn-primary me-3"
                >
                    <i className="bi bi-x-mark"></i>
                    Update
                </button>
                <button
                    type="button"
                    className="btn btn-sm btn-danger"
                    data-bs-toggle="modal"
                    data-bs-target="#exampleModal">
                    <i className="bi bi-x-mark"></i>
                    Delete
                </button>
            </div>

            <Modal data={{
                head_title: "Confirm Delete",
                body_message: "Are you sure to delete : " + data.title,
                click_handle: handleDelete,
                click_message: "Delete"
            }} />

            <div className="mb-4 d-flex align-items-center border-bottom py-3">
                <div
                    className="b-light rounded-3 me-3 d-flex justify-content-center align-items-center"
                    style={{ width: '60px', height: '60px' }}>
                    WD
                </div>
                <div className="">
                    <p className="h4 c-primary mb-2">{data.title}</p>
                    <span className="mb-1 me-4"><i className="bi bi-geo-alt-fill me-2"></i>{data.location}</span>
                    <span className="mb-1 me-4"><i className="bi bi-clock-fill me-2"></i>{data.job_type}</span>
                    <span className="mb-1"><i className="bi bi-calendar-fill me-2"></i>{data.experience}</span>
                </div>
            </div>

            <div className="py-3">
                <h5 className="fw-normal c-secondary mb-3">
                    <i className="bi bi-arrow-right me-2 fs-5"></i>
                    Job Description
                </h5>
                <div>{parse(data.job_description)}</div>
                <br /> <br />
                <h5 className="fw-normal c-secondary mb-3">
                    <i className="bi bi-arrow-right me-2 fs-5"></i>
                    Responsibilities
                </h5>
                <div>{parse(data.responsibilities)}</div>
                <br /> <br />
                <h5 className="fw-normal c-secondary mb-3">
                    <i className="bi bi-arrow-right me-2 fs-5"></i>
                    Qualifications
                </h5>
                <div>{parse(data.qualifications)}</div>
            </div>
        </>
    )
}