import React, { useState, useEffect } from "react";
import $ from "jquery"
import conf from "../config.json"
import { Link, useNavigate } from "react-router-dom";


export default function ContactFormData() {
    const navigate = useNavigate();
    const [contactMessageList, setContactMessageList] = useState([]);
    const [message, setMessage] = useState('');

    function handleClick(data) {
        navigate('/a/contact/view', { state: data });
    }

    useEffect(() => {
        const header = {
            "Authorization": "Bearer " + localStorage.getItem("accessToken")
        }
        $.ajax({
            url: conf.api_endpoint.contact,
            method: 'GET',
            headers: header,
            success: function (response) {
                if (response.success) {
                    if (response.response) {
                        response.response.forEach(element => {
                            setContactMessageList(contactMessageList => [
                                ...contactMessageList,
                                element
                            ]);
                        });
                    }
                    else {
                        setMessage("No Data to Show")
                    }
                }
            },
            error: function (errors) {
                if (errors.status === 401) {
                    navigate("/login")
                }
                else {
                    const err = errors.responseJSON.errors;
                    setMessage("Something went wrong! Try Refresh or Contact adminstrator");
                    console.log(err);
                }
            }
        });
    }, []);

    setTimeout(() => {
        setMessage("");
    }, 3000)

    return (
        <>
            {
                message &&
                <div className="alert alert-warning py-2 px-3">
                    <p className="mb-0">{message}</p>
                </div>
            }
            <div className="mb-4 d-flex justify-content-between">
                <span className="pb-2 h4 border-bottom">Contact Messages</span>

            </div>
            <div>
                <table className="table border table-striped table-hover table-sm">
                    <thead className="table-dark">
                        <tr>
                            <th scope="col" className="col-1">S.no</th>
                            <th scope="col" >Subject</th>
                            <th scope="col" className="col-3">Name</th>
                            <th scope="col" className="col-3">Email</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            contactMessageList.map((data, i) => {
                                return (
                                    <tr key={i}>
                                        <th scope="row">{i + 1}</th>
                                        <td>
                                            <button
                                                className="btn btn-link p-0"
                                                onClick={() => handleClick({
                                                    data: data,
                                                })}
                                            >{data.subject}</button>
                                        </td>
                                        <td>{data.full_name}</td>
                                        <td>{data.email}</td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </div>
        </>
    )
}