import React, { useState } from "react";
import Header from "../assist/Header"
import Footer from "../assist/Footer"
import Banner from "../assist/Banner"
import banner_image from "../assets/images/contact-header.webp"
import $ from "jquery"
import conf from "../config.json"

export default function Home() {
    const [inputs, setInputs] = useState({});
    const [errors, setErrors] = useState({});
    const [formStatus, setFormStatus] = useState(false);

    const handleChange = (e) => {
        setInputs(inputs => ({ ...inputs, [e.target.name]: e.target.value }));
    }

    const submitForm = (e) => {
        e.preventDefault();
        setErrors({});

        const formData = new FormData();
        for (var key in inputs) {
            formData.append(key, inputs[key]);
        }

        $.ajax({
            url: conf.api_endpoint.contact,
            method: "POST",
            data: formData,
            success: (response) => {
                if (response.success) {
                    setFormStatus(true);
                    setTimeout(() => {
                        setFormStatus(false);
                    }, 3000);
                }
            },
            error: (errors) => {
                const err = errors.responseJSON.errors;
                err.forEach(element => {
                    setErrors(er => ({ ...er, [element.field]: element.message }))
                });
            },
            processData: false,
            contentType: false
        });
        setInputs({});
        setFormStatus(true);
        setTimeout(() => {
            setFormStatus(false);
        }, 3000);

    }

    return (
        <>
            <Header />
            <Banner banner_source={banner_image} banner_alt={'Careers Banner'} data={"Contact Us"} />

            <section id="contact" className="contact py-5 b-light">
                <div className="container position-relative">
                    <div className="row gy-4 d-flex justify-content-end">
                        <div className="col-lg-5" id="contact-info">
                            <div className="info-item d-flex">
                                <i className="bi bi-geo-alt flex-shrink-0"></i>
                                <div>
                                    <h4>Location:</h4>
                                    <p>1001, S-3, Rishabh, Cloud 9, Vaishali, Ghaziabad, India</p>
                                </div>
                            </div>

                            <div className="info-item d-flex">
                                <i className="bi bi-envelope flex-shrink-0"></i>
                                <div>
                                    <h4>Email:</h4>
                                    <p>videalpha@gmail.com</p>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-6" data-aos-delay="250">
                            <form onSubmit={submitForm} id="web-fs-contact-form" className="web-fs-contact-form">
                                {
                                    formStatus &&
                                    <div className="alert alert-success">
                                        <p className="mb-0">
                                            <i className="bi bi-check-circle me-2"></i>
                                            Thanks for your message ! We will contact you soon.
                                        </p>
                                    </div>
                                }
                                <div className="row">
                                    <div className="col-md-6 form-group">
                                        <input
                                            type="text"
                                            name="full_name"
                                            className="form-control"
                                            id="full_name"
                                            placeholder="Your Name"
                                            required
                                            value={inputs.full_name || ""}
                                            onChange={handleChange}
                                        />
                                        {
                                            errors.Name &&
                                            <small className="text-danger">{errors.Name}</small>
                                        }
                                    </div>
                                    <div className="col-md-6 form-group mt-3 mt-md-0">
                                        <input
                                            type="email"
                                            className="form-control"
                                            name="email"
                                            id="email"
                                            placeholder="Your Email"
                                            value={inputs.email || ""}
                                            onChange={handleChange}
                                        />
                                        {
                                            errors.Email &&
                                            <small className="text-danger">{errors.Email}</small>
                                        }
                                    </div>
                                </div>
                                <div className="form-group mt-3">
                                    <input
                                        type="text"
                                        className="form-control"
                                        name="subject"
                                        id="subject"
                                        placeholder="Subject"
                                        required
                                        value={inputs.subject || ""}
                                        onChange={handleChange}
                                    />
                                    {
                                        errors.Subject &&
                                        <small className="text-danger">{errors.Subject}</small>
                                    }
                                </div>
                                <div className="form-group mt-3">
                                    <textarea
                                        className="form-control"
                                        name="message"
                                        rows="5"
                                        placeholder="Message"
                                        required
                                        value={inputs.message || ""}
                                        onChange={handleChange}
                                    />
                                    {
                                        errors.Message &&
                                        <small className="text-danger">{errors.Message}</small>
                                    }
                                </div>
                                <div className="my-3">
                                    <div className="sent-message">Thanks for your message !<br />We will get back to you soon</div>
                                </div>
                                <div className="text-center">
                                    <button type="submit" className="btn c-primary">Send Message</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </>
    );
}