
import { Link } from "react-router-dom";
import logo from "../assets/images/logo.png"
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";


const Header = ({ bgWhite }) => {
    const navigate = useNavigate();
    const [isScrolled, setIsScrolled] = useState(false);
    const [isLoggedIn, setIsLoggedIn] = useState(localStorage.getItem('is_authenticated'));


    useEffect(() => {
        const handleScroll = () => {
            if (window.pageYOffset > 70) {
                setIsScrolled(true);
            } else {
                setIsScrolled(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const handleLogout = () => {
        localStorage.removeItem('is_authenticated');
        localStorage.removeItem('accessToken');
        localStorage.removeItem('user');
        setIsLoggedIn(false);
        navigate("/login")
    }

    return (
        <>
            <header className={`navbar nav-transparent border-bottom bg-white navbar-expand-lg navbar-light fixed-top ${isScrolled ? 'navbar-scrolled' : ''}${bgWhite ? 'b-light' : ''}`} id="headerNav">
                <div className="container">
                    <div className="logo">
                        <Link to="/">
                            <img src={logo} alt="logo" width={85} />
                        </Link>
                    </div>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarNav">
                        <ul className="navbar-nav ms-auto d-flex align-items-center">
                            <li className="mx-2">
                                <Link to="/" className="nav-link px-2">Home</Link>
                            </li>
                            <li className="mx-2">
                                <Link to="/about" className="nav-link px-2">About</Link>
                            </li>
                            <li className="mx-2">
                                <Link to="/contact" className="nav-link px-2">Contact</Link>
                            </li>
                            <li className="mx-2">
                                <Link to="/services" className="nav-link px-2">Services</Link>
                            </li>
                            <li className="mx-2">
                                <Link to="/careers" className="nav-link px-2">Careers</Link>
                            </li>
                            {
                                isLoggedIn &&
                                <div className="dropdown">
                                    <button className="btn dropdown-toggle" id="dropdownMenuLink" data-bs-toggle="dropdown" aria-expanded="false">
                                        <i className="bi bi-person-circle fs-4"></i>
                                    </button>

                                    <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
                                        <li><Link className="dropdown-item" to={"/admin"}>Admin</Link></li>
                                        <li><button className="dropdown-item" onClick={handleLogout}>Logout</button></li>
                                    </ul>
                                </div>
                            }

                        </ul>
                    </div>
                </div>
            </header>
            <div style={{ padding: '30px 0' }}></div>
        </>
    );
};

export default Header;
