import React from "react"
import "bootstrap/dist/js/bootstrap.min.js"
import parse from 'html-react-parser';


import { useNavigate } from 'react-router-dom';

export default function Accordion({ joblist }) {

    const navigate = useNavigate();
    function handleClick(data) {
        navigate('/careers/apply', { state: data });
    }

    return (
        <div className="py-2">
            <div className="accordion career-accordian" id="careerAccordion">
                {joblist.map((item, i) => {
                    return (
                        <div className="accordion-item" key={i}>
                            <h2 className="accordion-header" id="headingOne">
                                <button className="accordion-button" type="button"
                                    data-bs-toggle="collapse"
                                    data-bs-target={'#collapse' + i}>
                                    <div className="p-3 w-100 c-secondary">
                                        <h4 className="mb-3 c-primary fw-normal">{item.title}</h4>
                                        <div className="d-md-flex">
                                            <div className="me-4 mb-3">
                                                <i className="bi bi-geo-alt-fill me-2"></i>
                                                {item.location}
                                            </div>

                                            <div className="me-4 mb-3">
                                                <i className="bi bi-clock-fill me-2"></i>
                                                {item.job_type}
                                            </div>

                                            <div className="me-4">
                                                <i className="bi bi-calendar-fill me-2"></i>
                                                (2 - 3 Years experience)
                                            </div>
                                        </div>
                                    </div>
                                </button>
                            </h2>
                            <div id={'collapse' + i} className="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                                <div className="accordion-body p-4 p-lg-5">
                                    <div className="row">
                                        <div className="col-lg-9">
                                            <h5 className="fw-normal c-secondary mb-3">
                                                <i className="bi bi-arrow-right me-2 fs-5"></i>
                                                Job Description
                                            </h5>
                                            <div>{parse(item.job_description)}</div>
                                            <br /> <br />
                                            <h5 className="fw-normal c-secondary mb-3">
                                                <i className="bi bi-arrow-right me-2 fs-5"></i>
                                                Responsibilities
                                            </h5>
                                            <div>{parse(item.responsibilities)}</div>
                                            <br /> <br />
                                            <h5 className="fw-normal c-secondary mb-3">
                                                <i className="bi bi-arrow-right me-2 fs-5"></i>
                                                Qualifications
                                            </h5>
                                            <div>{parse(item.qualifications)}</div>

                                        </div>
                                        <div className="col-lg-3 b-light p-4 rounded-4 border">
                                            <h5 className="fw-normal c-secondary mb-3">
                                                <i className="bi bi-arrow-right me-2 fs-5">
                                                </i>Job Summary
                                            </h5>
                                            <hr className="mb-4" />
                                            <p className="me-2">
                                                <i className="c-gray bi bi-geo-alt-fill me-3 fs-6"></i>
                                                {item.location}
                                            </p>

                                            <p className="me-2">
                                                <i className="c-gray bi bi-clock-fill me-3 fs-6"></i>
                                                {item.job_type}
                                            </p>

                                            <p className="me-2">
                                                <i className="c-gray bi bi-calendar-fill me-3 fs-6"></i>
                                                (2 - 3 Years experience)
                                            </p>

                                            <p className="me-2">
                                                <i className="c-gray bi bi-calendar-event-fill me-3 fs-6"></i>
                                                {item.posted_on}
                                            </p>

                                            <p className="me-2">
                                                <i className="c-gray bi bi-calendar-x-fill me-3 fs-6"></i>
                                                {item.valid_upto}
                                            </p>
                                            <button
                                                className="btn b-primary c-light w-100 mt-3"
                                                onClick={() => handleClick({
                                                    title: item.title,
                                                    id: item.id,
                                                })}
                                            >Apply Now</button>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
}