import React from "react";
import ReactDOM from "react-dom/client";
import "bootstrap/dist/css/bootstrap.min.css"
import "bootstrap-icons/font/bootstrap-icons.css"
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import "./assets/css/main.css"


// ======== Pages ======== //
import Home from "./pages/Home";
import Contact from "./pages/Contact";
import Careers from "./pages/Careers";
import Services from "./pages/Services";
import NoPage from "./errors/NoPage";


// ======== Forms ======== //
import JobForm from "./forms/JobForm";
import LoginForm from "./auth/LoginForm";


// ======== Admin ======== //
import Admin from "./admin/Admin";
import JobApplications from "./admin/JobsApplications";
import JobData from "./admin/JobData";
import ContactFormData from "./admin/ContactMessages";
import CreateJob from "./forms/JobCreateForm"
import ViewJob from "./admin/view/JobView"
import ContactView from "./admin/view/ContactView"
import JobApplicationView from "./admin/view/JobApplicationView"


export default function App() {
  return (
    <Router>
      <Routes>
        <Route index element={<Home />} />
        <Route path="*" element={<NoPage />} />
        <Route path="contact" element={<Contact />} />
        <Route path="careers" element={<Careers />} />
        <Route path="services/:type?/:id?" element={<Services />} />
        <Route path="careers/apply" element={<JobForm />} />


        {/* ========= Admin Routes ========== */}
        <Route path="login" element={<LoginForm />} />

        <Route
          path="admin"
          element={
            localStorage.getItem("is_authenticated") ?
              <Admin /> :
              <Navigate replace to={"/login"} />
          } />

        <Route path="a/job-applications" element={<Admin component={<JobApplications />} />} />
        <Route path="a/apply/view" element={<Admin component={<JobApplicationView />} />} />


        <Route path="a/contact-messages" element={<Admin component={<ContactFormData />} />} />
        <Route path="a/contact/view" element={<Admin component={<ContactView />} />} />

        <Route path="a/jobs" element={<Admin component={<JobData />} />} />
        <Route path="a/jobs/create" element={<Admin component={<CreateJob />} />} />
        <Route path="a/jobs/view" element={<Admin component={<ViewJob />} />} />
      </Routes>
    </Router>
  )
}


const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<App />);